import { Component, OnInit } from '@angular/core';
import {FormControl} from '@angular/forms';
declare var jQuery: any;

@Component({
  selector: 'ic-tasks',
  templateUrl: './tasks.container.html',
  styleUrls: ['./tasks.container.scss']
})
export class TasksContainer implements OnInit {
  date = new FormControl(new Date());
  constructor() { }

  ngOnInit() {
    setTimeout(()=>{
      jQuery('.dd').nestable();

      jQuery('.dd').on('change', function () {
        var $this = jQuery(this);
        var serializedData = JSON.stringify(jQuery($this).nestable('serialize'));

        $this.parents('div.body').find('textarea').val(serializedData);
      });

      jQuery('.dd4').nestable();

      jQuery('.dd4').on('change', function () {
        var $this = jQuery(this);
        var serializedData = JSON.stringify(jQuery($this).nestable('serialize'));
      });

/*
      jQuery('.todo_list').nestable();

      jQuery('.todo_list').on('change', function () {
        var $this = jQuery(this);
        var serializedData = JSON.stringify(jQuery($this).nestable('serialize'));

        $this.parents('div.body').find('textarea').val(serializedData);
      });
*/

    },1000)
  }

}
